<script>
import { generateId } from "@/utils/util";

export default {
  name: "LayoutSetting",
  props: ["layout", "entityItems", "entity", "fieldItems"],
  data() {
    return {
      activeName: "first",
    };
  },
  computed: {
    options() {
      let list = [];
      this.entityItems.forEach((item) => {
        if (item.is_childtable == 1 && item.name != this.entity.name) {
          list.push(item);
        }
      });
      return list == undefined ? [] : list;
    },
    fieldsList() {
      let list = [];
      this.fieldItems.forEach((item) => {
        if (item.name != this.layout.activeFieldWrapper.name) {
          list.push(item);
        }
      });
      return list;
    },
  },

  watch: {},
  created() {
    console.log(this);
  },
  methods: {
    handleChange(value) {
      if (this.layout.relationFields == undefined)
        this.layout.relationFields = [];
      let index = this.layout.relationFields.findIndex(
        (obj) => obj.id === this.layout.activeFieldWrapper.id
      );
      if (index >= 0) {
        this.$set(
          this.layout.relationFields,
          index,
          this.layout.activeFieldWrapper
        );
      } else {
        this.layout.relationFields.push(this.layout.activeFieldWrapper);
      }
    },
    toggleLabelAlign(align) {
      this.layout.labelAlign = align;
    },

    spanChanged(currentRow, cell, cellIdx, newValue, oldValue) {
      //console.log(cellIdx)
      //console.log(this.$refs.inputNumberComps)
      let spanSum = 0;
      currentRow.cells.forEach((cellItem, idx) => {
        spanSum += cellItem.span;
        console.log(idx);
      });
      if (spanSum > 24) {
        // this.$set(cell, 'span', oldValue)
        // this.$forceUpdate()
        const curInputComp = this.$refs.inputNumberComps[cellIdx];
        if (curInputComp) {
          console.log(oldValue);
          // curInputComp.setCurrentValue(oldValue)
          // curInputComp.decrease()
        }
        this.$alert("24", "").catch(() => {});
      }
    },

    deleteCell(currentRow, cellIdx) {
      if (!!currentRow && !!currentRow.cells) {
        currentRow.cells.splice(cellIdx, 1);
      }
    },

    addNewCell(currentRow) {
      const cells = currentRow.cells;
      if (!!cells && cells.length > 0) {
        let spanSum = 0;
        cells.forEach((cell, idx) => {
          spanSum += cell.span;
          console.log(idx);
        });

        if (spanSum >= 24) {
          this.$message.info("24");
        } else {
          const newSpan = 24 - spanSum > 6 ? 6 : 24 - spanSum;
          currentRow.cells.push({
            id: "cell" + generateId(),
            span: newSpan,
            fields: [],
          });
        }
      } else {
        currentRow.cells = [{ id: "cell" + generateId(), span: 6, fields: [] }];
      }
    },

    labelWidthChanged(currentValue) {
      if (this.layout.activeFieldWrapper) {
        // this.layout.activeFieldWrapper.labelWidth = currentValue
        if (currentValue !== this.layout.labelWidth) {
          this.$set(this.layout.activeFieldWrapper, "labelWidth", currentValue);
        } else {
          this.$set(this.layout.activeFieldWrapper, "labelWidth", null);
        }
      }
    },

    formLabelWidthChanged() {
      this.$forceUpdate(); /*  */
    },

    handleTitleShowStatus(value) {
      if (value === false) {
        this.layout.activeSection.showArrowIcon = false;
        this.layout.activeSection.showSplitter = false;
        this.$forceUpdate();
      }
    },
  },
};
</script>

