        width="460px"
      >
        <div class="name-field-hint">
          <i class="el-icon-bell"></i>
          (Text)
        </div>
        <SimpleTable
          :show-pagination="false"
          :show-check-box="false"
          :table-size="'mini'"
          :columns="nameFieldColumns"
          :data="nameFieldData"
          :max-height="420"
        >
          <el-table-column
            slot="table_operation"
            align="center"
            label=""
            width="150"
            :resizable="false"
          >
            <template slot-scope="scope">
              <el-button
                v-if="!!!scope.row.nameFieldFlag"
                class=""
                icon="el-icon-check"
                @click="selectNameField(scope.row)"
              >
                
              </el-button>
              <el-button v-else :disabled="true"></el-button>
            </template>
          </el-table-column>
        </SimpleTable>
      </el-dialog>

      <el-dialog
        v-if="showNewFieldDialogFlag"
        :title="' / ' + curEditorType"
        :visible.sync="showNewFieldDialogFlag"
        :show-close="true"
        :destroy-on-close="true"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        class="no-padding"
        width="620px"
        append-to-body
      >
        <component
          :is="curFWEditor"
          :entity="entity"
          :showing-in-dialog="true"
          @fieldSaved="onFieldSaved"
          @cancelSave="onCancelSaveField"
        ></component>
      </el-dialog>

      <el-dialog
        v-if="showEditFieldDialogFlag"
        :title="' / ' + curEditorType"
        :visible.sync="showEditFieldDialogFlag"
        :show-close="true"
        :destroy-on-close="true"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        class="no-padding"
        width="620px"
        append-to-body
      >
        <component
          :is="curFWEditor"
          ref="editField"
          :entity="entity"
          :showing-in-dialog="true"
          :field-name="editingFieldName"
          :field-state="2"
          @fieldSaved="onFieldSaved"
          @cancelSave="onCancelSaveField"
        ></component>
      </el-dialog>

      <el-dialog
        v-if="showEntityPropsDialogFlag"
        title=""
        :visible.sync="showEntityPropsDialogFlag"
        :show-close="false"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :destroy-on-close="true"
        append-to-body
      >
        <EntityPropEditor
          ref="EPEditor"
          :entity-props="entityProps"
          :show-title="false"
          :filter-entity-method="filterMainEntity"
        ></EntityPropEditor>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" size="small" @click="saveEntity">
             
          </el-button>
          <el-button size="small" @click="cancelSaveEntity"> </el-button>
